import { useState, useEffect } from "react"
import { CircleDollarSign, ShoppingBasket, Star, Tag, TrendingUp, Users, Package } from "lucide-react"
import { AreaChart, Area, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer, BarChart, Bar, PieChart, Pie, Cell } from "recharts"

export default function StoreDashboard() {
    const currency = '$'
    const [loading, setLoading] = useState(false)
    const [dashboardData, setDashboardData] = useState({
        totalOrders: 145,
        totalEarnings: 12500.50,
        totalProducts: 28,
        ratings: [],
        ordersByStatus: {
            ORDER_PLACED: 12,
            PROCESSING: 8,
            SHIPPED: 15,
            DELIVERED: 110
        },
        paymentBreakdown: {
            COD: 85,
            STRIPE: 60
        },
        productSales: [
            { id: '1', name: 'Wheat Flour', image: 'https://via.placeholder.com/50', totalSold: 156, revenue: 7020, category: 'Fresh' },
            { id: '2', name: 'Rice Flour', image: 'https://via.placeholder.com/50', totalSold: 134, revenue: 5360, category: 'Fresh' },
            { id: '3', name: 'Upvas Flour', image: 'https://via.placeholder.com/50', totalSold: 98, revenue: 4410, category: 'Upvas' }
        ],
        revenueByDate: {
            '12/1/2024': 450.50,
            '12/2/2024': 680.25,
            '12/3/2024': 520.00,
            '12/4/2024': 890.75,
            '12/5/2024': 720.30
        },
        categoryStats: {
            'Fresh': { totalSold: 320, revenue: 14400, products: 12 },
            'Upvas': { totalSold: 180, revenue: 8100, products: 8 },
            'Special': { totalSold: 95, revenue: 4275, products: 8 }
        },
        uniqueCustomers: 89,
        avgOrderValue: 86.21,
        recentActivity: [
            { id: '1', customer: 'John Doe', total: 125.50, status: 'DELIVERED', date: new Date() },
            { id: '2', customer: 'Jane Smith', total: 89.99, status: 'SHIPPED', date: new Date() }
        ]
    })

    const statsCards = [
        { title: 'Total Products', value: dashboardData.totalProducts, icon: ShoppingBasket, color: 'bg-blue-500' },
        { title: 'Total Earnings', value: `${currency}${dashboardData.totalEarnings.toLocaleString()}`, icon: CircleDollarSign, color: 'bg-green-500' },
        { title: 'Total Orders', value: dashboardData.totalOrders, icon: Tag, color: 'bg-purple-500' },
        { title: 'Unique Customers', value: dashboardData.uniqueCustomers, icon: Users, color: 'bg-orange-500' },
        { title: 'Avg Order Value', value: `${currency}${dashboardData.avgOrderValue}`, icon: TrendingUp, color: 'bg-pink-500' },
        { title: 'Total Ratings', value: dashboardData.ratings.length, icon: Star, color: 'bg-yellow-500' }
    ]

    const revenueChartData = Object.entries(dashboardData.revenueByDate).map(([date, revenue]) => ({
        date,
        revenue
    }))

    const orderStatusData = Object.entries(dashboardData.ordersByStatus).map(([status, count]) => ({
        name: status.replace('_', ' '),
        value: count
    }))

    const COLORS = ['#3b82f6', '#f59e0b', '#8b5cf6', '#10b981']

    const categoryChartData = Object.entries(dashboardData.categoryStats).map(([category, stats]) => ({
        category,
        revenue: stats.revenue,
        sold: stats.totalSold
    }))

    return (
        <div className="p-6 space-y-6 max-w-7xl">
            <h1 className="text-2xl font-semibold">
                Seller <span className="text-slate-800">Dashboard</span>
            </h1>

            {/* Stats Cards */}
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
                {statsCards.map((stat, index) => (
                    <div key={index} className="bg-white p-5 rounded-lg shadow-sm border border-slate-200 flex items-center justify-between">
                        <div>
                            <p className="text-sm text-slate-500">{stat.title}</p>
                            <p className="text-2xl font-semibold text-slate-800 mt-1">{stat.value}</p>
                        </div>
                        <div className={`${stat.color} p-3 rounded-full`}>
                            <stat.icon className="text-white" size={24} />
                        </div>
                    </div>
                ))}
            </div>

            {/* Revenue Chart */}
            <div className="bg-white p-6 rounded-lg shadow-sm border border-slate-200">
                <h2 className="text-lg font-semibold mb-4">Revenue Trend (Last 30 Days)</h2>
                <ResponsiveContainer width="100%" height={300}>
                    <AreaChart data={revenueChartData}>
                        <CartesianGrid strokeDasharray="3 3" />
                        <XAxis dataKey="date" />
                        <YAxis />
                        <Tooltip />
                        <Area type="monotone" dataKey="revenue" stroke="#10b981" fill="#10b981" fillOpacity={0.2} />
                    </AreaChart>
                </ResponsiveContainer>
            </div>

            <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
                {/* Order Status Breakdown */}
                <div className="bg-white p-6 rounded-lg shadow-sm border border-slate-200">
                    <h2 className="text-lg font-semibold mb-4">Order Status</h2>
                    <ResponsiveContainer width="100%" height={250}>
                        <PieChart>
                            <Pie
                                data={orderStatusData}
                                cx="50%"
                                cy="50%"
                                labelLine={false}
                                label={({ name, percent }) => `${name}: ${(percent * 100).toFixed(0)}%`}
                                outerRadius={80}
                                fill="#8884d8"
                                dataKey="value"
                            >
                                {orderStatusData.map((entry, index) => (
                                    <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
                                ))}
                            </Pie>
                            <Tooltip />
                        </PieChart>
                    </ResponsiveContainer>
                </div>

                {/* Category Performance */}
                <div className="bg-white p-6 rounded-lg shadow-sm border border-slate-200">
                    <h2 className="text-lg font-semibold mb-4">Category Revenue</h2>
                    <ResponsiveContainer width="100%" height={250}>
                        <BarChart data={categoryChartData}>
                            <CartesianGrid strokeDasharray="3 3" />
                            <XAxis dataKey="category" />
                            <YAxis />
                            <Tooltip />
                            <Bar dataKey="revenue" fill="#3b82f6" />
                        </BarChart>
                    </ResponsiveContainer>
                </div>
            </div>

            {/* Top Selling Products */}
            <div className="bg-white p-6 rounded-lg shadow-sm border border-slate-200">
                <h2 className="text-lg font-semibold mb-4">Top Selling Products</h2>
                <div className="overflow-x-auto">
                    <table className="w-full text-sm">
                        <thead className="bg-slate-50">
                            <tr>
                                <th className="px-4 py-3 text-left">Product</th>
                                <th className="px-4 py-3 text-left">Category</th>
                                <th className="px-4 py-3 text-right">Units Sold</th>
                                <th className="px-4 py-3 text-right">Revenue</th>
                            </tr>
                        </thead>
                        <tbody className="divide-y">
                            {dashboardData.productSales.map((product) => (
                                <tr key={product.id} className="hover:bg-slate-50">
                                    <td className="px-4 py-3">
                                        <div className="flex items-center gap-3">
                                            <img src={product.image} alt="" className="w-10 h-10 rounded" />
                                            <span className="font-medium">{product.name}</span>
                                        </div>
                                    </td>
                                    <td className="px-4 py-3">{product.category}</td>
                                    <td className="px-4 py-3 text-right font-semibold">{product.totalSold}</td>
                                    <td className="px-4 py-3 text-right text-green-600 font-semibold">
                                        {currency}{product.revenue.toLocaleString()}
                                    </td>
                                </tr>
                            ))}
                        </tbody>
                    </table>
                </div>
            </div>

            {/* Payment Methods */}
            <div className="bg-white p-6 rounded-lg shadow-sm border border-slate-200">
                <h2 className="text-lg font-semibold mb-4">Payment Methods</h2>
                <div className="grid grid-cols-2 gap-4">
                    <div className="border rounded-lg p-4 text-center">
                        <p className="text-sm text-slate-500">Cash on Delivery</p>
                        <p className="text-3xl font-bold text-slate-800 mt-2">{dashboardData.paymentBreakdown.COD}</p>
                        <p className="text-xs text-slate-400 mt-1">orders</p>
                    </div>
                    <div className="border rounded-lg p-4 text-center">
                        <p className="text-sm text-slate-500">Stripe</p>
                        <p className="text-3xl font-bold text-slate-800 mt-2">{dashboardData.paymentBreakdown.STRIPE}</p>
                        <p className="text-xs text-slate-400 mt-1">orders</p>
                    </div>
                </div>
            </div>

            {/* Recent Activity */}
            <div className="bg-white p-6 rounded-lg shadow-sm border border-slate-200">
                <h2 className="text-lg font-semibold mb-4">Recent Orders</h2>
                <div className="space-y-3">
                    {dashboardData.recentActivity.map((activity) => (
                        <div key={activity.id} className="flex items-center justify-between p-3 bg-slate-50 rounded-lg">
                            <div>
                                <p className="font-medium">{activity.customer}</p>
                                <p className="text-xs text-slate-500">{new Date(activity.date).toLocaleString()}</p>
                            </div>
                            <div className="text-right">
                                <p className="font-semibold text-green-600">{currency}{activity.total}</p>
                                <span className="text-xs px-2 py-1 rounded-full bg-blue-100 text-blue-700">
                                    {activity.status}
                                </span>
                            </div>
                        </div>
                    ))}
                </div>
            </div>
        </div>
    )
}